./mixins\";\n@import \"./variables\";\n\n// postcss-bem-linter: define card\n\n.mdc-card {\n  @include mdc-card-fill-color(surface);\n  @include mdc-card-corner-radius(2px);\n  @include mdc-elevation(2);\n  @include mdc-card-container-layout_;\n}\n\n.mdc-card--outlined {\n  @include mdc-elevation(0);\n  @include mdc-card-outline($mdc-card-outline-color);\n}\n\n//\n// Media\n//\n\n.mdc-card__media {\n  @include mdc-card-media-aspect-ratio-base_;\n\n  position: relative; // Child element `__media-content` has `position: absolute`\n  box-sizing: border-box;\n  background-repeat: no-repeat;\n  background-position: center;\n  background-size: cover;\n}\n\n.mdc-card__media:first-child {\n  border-top-left-radius: inherit;\n  border-top-right-radius: inherit;\n}\n\n.mdc-card__media:last-child {\n  border-bottom-left-radius: inherit;\n  border-bottom-right-radius: inherit;\n}\n\n.mdc-card__media--square {\n  @include mdc-card-media-aspect-ratio(1, 1);\n}\n\n.mdc-card__media--16-9 {\n  @include mdc-card-media-aspect-ratio(16, 9);\n}\n\n.mdc-card__media-content {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  box-sizing: border-box;\n}\n\n//\n// Primary action\n//\n\n.mdc-card__primary-action {\n  @include mdc-ripple-surface;\n  @include mdc-ripple-radius-bounded;\n  @include mdc-states;\n  @include mdc-card-container-layout_;\n\n  position: relative; // Needed to prevent the ripple wash from overflowing the container in IE and Edge\n  outline: none;\n  color: inherit;\n  text-decoration: none;\n  cursor: pointer;\n  overflow: hidden;\n}\n\n.mdc-card__primary-action:first-child {\n  border-top-left-radius: inherit;\n  border-top-right-radius: inherit;\n}\n\n.mdc-card__primary-action:last-child {\n  border-bottom-left-radius: inherit;\n  border-bottom-right-radius: inherit;\n}\n\n//\n// Action row\n//\n\n.mdc-card__actions {\n  @include mdc-card-actions-layout_;\n\n  min-height: 52px;\n  padding: 8px;\n}\n\n.mdc-card__actions--full-bleed {\n  padding: 0;\n}\n\n.mdc-card__action-buttons,\n.mdc-card__action-icons {\n  @include mdc-card-actions-layout_;\n}\n\n.mdc-card__action-icons {\n  @include mdc-theme-prop(color, text-icon-on-background);\n\n  flex-grow: 1;\n  justify-content: flex-end;\n}\n\n.mdc-card__action-buttons + .mdc-card__action-icons {\n  @include mdc-rtl-reflexive-box(margin, left, 16px);\n}\n\n//\n// Action items\n//\n\n.mdc-card__action {\n  @include mdc-card-actions-layout_(inline-flex);\n\n  justify-content: center;\n  cursor: pointer;\n  user-select: none;\n\n  &:focus {\n    outline: none;\n  }\n}\n\n//\n// Action buttons\n//\n\n.mdc-card__action--button {\n  @include mdc-rtl-reflexive-box(margin, right, 8px);\n\n  padding: 0 8px;\n\n  &:last-child {\n    @include mdc-rtl-reflexive-box(margin, right, 0);\n  }\n}\n\n.mdc-card__actions--full-bleed .mdc-card__action--button {\n  justify-content: space-between;\n  width: 100%;\n  height: auto;\n  max-height: none;\n  margin: 0;\n  padding: 8px 16px;\n  text-align: left;\n\n  @include mdc-rtl {\n    text-align: right;\n  }\n}\n\n//\n// Action icons\n//\n\n.mdc-card__action--icon {\n  // Icon toggles are taller than buttons, so we need to adjust their margins to prevent the action row from expanding.\n  margin: -6px 0;\n\n  // Same padding as mdc-icon-button.\n  padding: 12px;\n}\n\n.mdc-card__action--icon:not(:disabled) {\n  @include mdc-theme-prop(color, text-icon-on-background);\n}\n\n// postcss-bem-linter: end\n","//\n// Copyright 2017 Google Inc. All Rights Reserved.\n//\n// Licensed under the Apache License, Version 2.0 (the \"License\");\n// you may not use this file except in compliance with the License.\n// You may obtain a copy of the License at\n//\n//      http://www.apache.org/licenses/LICENSE-2.0\n//\n// Unless required by applicable law or agreed to in writing, software\n// distributed under the License is distributed on an \"AS IS\" BASIS,\n// WITHOUT WARRANTIES OR CONDITIONS OF ANY KIND, either express or implied.\n// See the License for the specific language governing permissions and\n// limitations under the License.\n//\n\n@import \"./variables\";\n\n// Applies the correct theme color style to the specified property.\n// $property is typically color or background-color, but can be any CSS property that accepts color values.\n// $style should be one of the map keys in $mdc-theme-property-values (_variables.scss), or a literal color value.\n// $edgeOptOut controls whether to feature-detect around Edge to avoid emitting CSS variables for it,\n// intended for use in cases where interactions with pseudo-element styles cause problems due to Edge bugs.\n@mixin mdc-theme-prop($property, $style, $important: false, $edgeOptOut: false) {\n  @if type-of($style) == \"color\" or $style == \"currentColor\" {\n    @if $important {\n      #{$property}: $style !important;\n    } @else {\n      #{$property}: $style;\n    }\n  } @else {\n    @if not map-has-key($mdc-theme-property-values, $style) {\n      @error \"Invalid style: '#{$style}'. Choose one of: #{map-keys($mdc-theme-property-values)}\";\n    }\n\n    $value: map-get($mdc-theme-property-values, $style);\n\n    @if $important {\n      #{$property}: $value !important;\n\n      @if $edgeOptOut {\n        // stylelint-disable max-nesting-depth\n        @at-root {\n          @supports not (-ms-ime-align:auto) {\n            // stylelint-disable scss/selector-no-redundant-nesting-selector\n            & {\n              /* @alternate */\n              #{$property}: var(--mdc-theme-#{$style}, $value) !important;\n            }\n            // stylelint-enable scss/selector-no-redundant-nesting-selector\n          }\n        }\n        // stylelint-enable max-nesting-depth\n      } @else {\n        /* @alternate */\n        #{$property}: var(--mdc-theme-#{$style}, $value) !important;\n      }\n    } @else {\n      #{$property}: $value;\n\n      @if $edgeOptOut {\n        // stylelint-disable max-nesting-depth\n        @at-root {\n          @supports not 